h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  display: block;
  margin: 0;
  padding: 0;
}
.hidden {
  display: none !important;
}

body {
  height: calc(100vh - 64px);
}

body #root {
  height: 100% !important;
}
::-webkit-scrollbar {
  display: none;
}
